export default {
	data() {
		return {
			content: {
				info: ''
			},
			//index: -1,
			picker: ['一周', '半年', '一年'],
			ColorList: this.ColorList,
			modalName: null,
			iconname: null,
			imgList: [],
			imgList1: [],
			locaction: ''
		};
	},
	
	methods:{				
		// PickerChange(e) {
		// 	this.index = e.detail.value
		// },
		
		// textareaAInput(e) {
		// 	this.textareaAValue = e.detail.value
		// },
		
		uploadImg: function(img, callback) {
			//上传图片逻辑,将图片链接传给回调函数
			callback(img)
		},
		
		//获取地理位置
		getLocation() {
			var _this = this;
			uni.chooseLocation({
			    success: function (res) {
			        //console.log('位置名称：' + res.name);
			        //console.log('详细地址：' + res.address);
			        // console.log('纬度：' + res.latitude);
			        // console.log('经度：' + res.longitude);
					_this.locaction = res.name;
			    }
			});
		},
		
		//商户标志
		Imagelist() {
			uni.chooseImage({
				count: 20, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '删除图片',
				content: '确定要删除吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		
		//风采照片
		Imagelist1() {
			uni.chooseImage({
				count: 20, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList1.length != 0) {
						this.imgList1 = this.imgList1.concat(res.tempFilePaths)
					} else {
						this.imgList1 = res.tempFilePaths
					}
				}
			});
		},
		ViewImage1(e) {
			uni.previewImage({
				urls: this.imgList1,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg1(e) {
			uni.showModal({
				title: '删除图片',
				content: '确定要删除吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList1.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		
		formSubmit: function(e) {
			//console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			if(!formdata.info){
				uni.showModal({
					content: '请填写内容',
					showCancel: false
				});
				return;
			}
			//开始发送数据
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: formdata.info
			    },
			    header: {
			        //'custom-header': 'hello'
					'content-type': 'application/json'
			    },
			    success: (res) => {
			        console.log(res.data);
			        this.text = 'request success';
			    }
			});
		},
		
	}
}
